import React, { Component } from 'react'

export class Header extends Component {
    titleStyle = {
        background: "#555",
        textAlign: "center",
        color: "white",
        marign: "0",
        fontSize: "3em"
    }
    render() {
    return (
      <div>
        <div style={this.titleStyle}>
          Todo List
        </div>
      </div>
    )
  }
}

export default Header
